*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  max-width: 100%;
  height: 100%;
}


header{
  width: 100%;
  height: 80px;
  border-bottom: 5px dotted black ;
  background-color: darkslategray;
}

header h1{
  padding-top: 10px;
  color: blue;
  text-align: center;
}

footer{
  width: 100%;
  height: 50px;
  background-color: darkslategray;

}

main{
  margin: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: beige;
  height: auto;
  padding-bottom: 50px;
}

main .headerText{
  padding-top: 20px;
  padding-top: 10px;

  border-bottom: 2px solid black;
  height: auto;
  width: 100%;
}

main .headerText h2{
  text-align: center;
}

main .content{
  margin-top: 10px;
  height: auto;
}

footer h5{
  padding-top: 10px;
  color: blue;
font-size: large;
  text-align: center;
}



form .inp{
 width: 100%;
 height: auto;
margin-top: 50px;
}

form .inp input[type="text"]{
width: 420px;
height: 30px;
border: none;
outline: none;
padding: 10px;
margin-right: 20px;
margin-left: 20px;
border-radius: 20px;
}

form .inp input[type="text"]:focus{
  border-bottom-color: brown;
  background-color: aqua;
}
form .inp button{
  width: 120px;
  height: 40px;
  border-radius: 40px;
  border: none;
  outline: none;
  background-color: deeppink;
  color: floralwhite;
  font-size: large;
  font-weight: 500;
  font-family: Georgia, 'Times New Roman', Times, serif;
  cursor: pointer;
}

form .inp button:hover{
  background-color: crimson;
}


section .dispalay{
  
  width: 100%;
  height: auto;
}

.dispalay ul li{
list-style: none;
padding: 20px;
width: 500px;
height: auto;
border: 2px solid black;
margin-top: 20px;
margin-bottom: 20px;
border-radius: 40px;
text-align: center;
background-color: gold;
}

.dispalay ul li button{
  width: 80px;
  height: 40px;
  border-radius: 40px;
  border: none;
  outline: none;
  background-color: deeppink;
  color: floralwhite;
  font-size: large;
  font-weight: 500;
  font-family: Georgia, 'Times New Roman', Times, serif;
  cursor: pointer;
  margin-left: 20px;
}

.view{
  width: 500px;
  height: 500px;
  border: 2px solid black;
  
}